import React from 'react'
import classes from './MessageButton.module.css'
import {Field, handleSubmit, reduxForm} from "redux-form";
import {maxLengthCreator, requiredField} from "../../../../utils/validators/validators";
import {Textarea} from "../../../common/FormsControls/FormsControls";



const MessageButton = (props) => {


  let addNewMessage = (values) => {
    props.onAddMessage(values.newMessageBody);

  };

  return (
    <AddMessageFormRedux onSubmit={addNewMessage} />
  )

};

const maxLengthCreator30 = maxLengthCreator(30);

const AddMessageForm = (props) => {

  return (

    <form onSubmit={props.handleSubmit} className={classes.messageFormWrapper}>

      <Field component={Textarea}
             name="newMessageBody"
             placeholder="Enter your message..."
             className={classes.textArea}
             validate={[requiredField, maxLengthCreator30]}

      />
      <button className={classes.btn}>Add Message</button>

    </form>

  )
};



const AddMessageFormRedux = reduxForm({form: "dialogAddMessageForm"})(AddMessageForm);

export default MessageButton;