import React from 'react'
import {addMessageActionCreator} from "../../../../redux/reducers/dialogs-reducer";
import MessageButton from "./MessageButton";
import {connect} from "react-redux";



let mapStateToProps = (state) => {
  return {
    newMessageText: state.dialogsPage.newMessageText

  }
};


let mapDispatchToProps = (dispatch) => {
  return {
    onAddMessage: (newMessageBody) => { dispatch(addMessageActionCreator(newMessageBody)) }
  }
};



const MessageButtonContainer = connect(mapStateToProps,mapDispatchToProps)(MessageButton);



export default MessageButtonContainer;