import React from 'react'
import classes from './Dialogs.module.css'
import DialogItem from "./DialogItem/DialogItem";
import Message from "./Message/Message";
import MessageButtonContainer from "./Message/MessageButton/MessageButtonContainer";
import {Redirect} from "react-router-dom";
import {withAuthReirect} from "../hoc/withAuthRedirect";
import {compose} from "redux";


const Dialogs = (props) => {



  let dialogsElements = props.store.getState().dialogsPage.dialogs
    .map((d) => <DialogItem name={d.name} id={d.id}/> );

  let messagesElements = props.store.getState().dialogsPage.messages
    .map( (m) => <Message message={m.message}/> );


  if (!props.isAuth) return <Redirect to={"/login"}/>;

  return (
    <div className={classes.dialogs}>
      <div className={classes.dialogsItems}>
        { dialogsElements }
      </div>

      <div className={classes.messages}>
        { messagesElements }
        <MessageButtonContainer store={props.store} />
      </div>
    </div>
  )

};



export default compose(
  withAuthReirect
)(Dialogs);